@import url("https://fonts.googleapis.com/css2?family=Anton&family=Cairo:wght@400;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  font-size: 1.8rem;
  min-height: 100%;
  background-color: #fff;
}

a {
  font-family: "Cairo", sans-serif;
  font-weight: bold;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.text-center {
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
}

h1, h2, h3 {
  font-family: "Anton", sans-serif;
}

h4 {
  font-family: "Cairo", sans-serif;
  font-size: 2.4rem;
  font-weight: bold;
}

p {
  font-family: "Cairo", sans-serif;
  font-size: 2rem;
}

a, h1, h2, h3, h4, p {
  color: #0F0F0F;
}

ul {
  list-style: none;
}